import {
  AnimatedKeyboardAvoidingViewRef,
  FullScreenWrapper,
  MyKeyboardAvoidingView,
  ScreenWrapper,
  UIInput,
  UITextInput,
} from '@true/ui';
import React, {memo, useCallback, useEffect, useRef, useState} from 'react';
import {
  Fonts,
  getUser,
  goBack,
  styled,
  ToastService,
  useAutoToastError,
  useBoolean,
  useNavigationParams,
} from '@true/core';
import Animated, {
  runOnUI,
  useAnimatedRef,
  useDerivedValue,
  useSharedValue,
} from 'react-native-reanimated';
import {ICreateEvent, initCreateEvent} from '@/store/events/type';
import {getChannels, useChannelsByQuery} from '@/store/channels';
import {AnimatedBottomSpace} from '@/components/AnimatedBottomSpace';
import {ImageDTO} from '@/components/CameraRollPicker/useGallery';
import {IBase64, uploadImage} from '@/utils/helper';
import {Buffer} from 'buffer';
import moment from 'moment';
import {useAsyncFn} from '@/hooks/useAsyncFn';
import {DiscardDialog} from '@/components/DiscardDialog';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import {MenuItem} from '@/components/MenuItem';
import {BottomSheetMenu} from '@/components/BottomSheets';
import {requestCreateEvent, requestEditEvent} from '@/store/events/function';
import {MyInputBackground} from '@/screens/CreateEventScreen/MyInputBackground';
import {AvatarWithSelectChannel} from '@/components/AvatarWithSelectChannel';
import {useUpdates} from '@/store/updates';
import {HeaderCreatePost} from '@/screens/CreatePostScreen/HeaderCreatePost';
import {ETypeUpdate} from '@/store/updates/type';
import {CheckboxWithText} from '@/components/CheckboxWithText';
import LoadingModal from '@/components/LoadingModal';

export interface CreateEventScreenParams {
  id: number;
}

export const CreateEventScreen = memo(function () {
  const {id} = useNavigationParams<CreateEventScreenParams>();

  const params = useSharedValue<ICreateEvent>(initCreateEvent);
  const ref = useRef<AnimatedKeyboardAvoidingViewRef>(null);
  const scrollRef = useAnimatedRef<any>();
  const [visibleDialog, showDialog, hideDialog] = useBoolean();
  const bottomSheetRef = useRef<BottomSheetModal | null>(null);
  const [label, setLabel] = useState('');
  const event = useUpdates(id)?.attachment;
  const imageRef = useRef<ImageDTO | null>(null);

  const channelsOption = useChannelsByQuery('joined').map(id => ({
    label: getChannels(id)?.name || 'Undefined channel',
    value: id,
  }));

  const onSelectBackground = useCallback(async (image: ImageDTO) => {
    imageRef.current = image;
    onChangeValue('cover-bg', image.url);
  }, []);

  const [{loading, error: createError}, onPressSubmit] =
    useAsyncFn(async () => {
      let imageUrl = params.value['cover-bg'];
      if (imageRef.current) {
        imageUrl = await uploadImage(imageRef.current);
      }

      const _params = {
        ...params.value,
        'cover-bg': imageUrl,
      };
      if (id !== -1) {
        await requestEditEvent(_params, id);
      } else {
        await requestCreateEvent(_params);
        ToastService.show('Create event successfully');
      }
      goBack();
    }, [id, imageRef.current, params]);
  useAutoToastError(createError);

  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value) => {
        'worklet';

        params.value = {
          ...params.value,
          [_key]: _value,
        };
      })(key, value);
    },
    [params],
  );

  const onGoBack = useCallback(() => {
    console.log('ss');
    goBack();
  }, []);

  useEffect(() => {
    let _params = {
      ...initCreateEvent,
    };
    if (event) {
      const coverImage: IBase64 = JSON.parse(
        Buffer.from(
          event?.cover ||
            'eyJ0eXBlIjoiIiwiY29sb3IiOiIiLCJvcGFjaXR5IjowLCJwZXJjZW50IjoxMDAsImltYWdlIjoiIiwidGV4dHMiOltdfQ',
          'base64',
        ).toString('utf-8'),
      );
      const listGuest = event?.followers
        .map((id: any) => `@${getUser(id)?.username}`)
        .join(' ');
      const listOwners = event?.owners
        .map((id: any) => `@${getUser(id)?.username}`)
        .join(' ');
      setLabel(getChannels(event?.channel_id)?.name || '');
      _params = {
        id: event?.id,
        address: event?.address || '',
        channel_id: event?.channel_id || 0,
        content: event?.content,
        'cover-bg': coverImage.image,
        guests: listGuest,
        name: event?.name || '',
        'file[]': event?.files || [],
        owners: listOwners,
        'time-date': moment(event?.time * 1000).format('DD/MM/YYYY'),
        'time-time': moment(event?.time * 1000).format('hh:mm'),
      };
    }
    params.value = _params;
  }, [event]);

  const animatedActiveBtn = useDerivedValue(() => {
    return !!params.value.channel_id;
  }, []);

  const renderItem = useCallback(
    (item: any, index: number) => {
      return (
        <MenuItem
          key={index}
          title={item.label}
          onPress={() => {
            onChangeValue('channel_id', item.value);
            setLabel(item.label);
            bottomSheetRef.current?.dismiss();
          }}
        />
      );
    },
    [bottomSheetRef],
  );

  return (
    <FullScreenWrapper isGrey>
      <MyKeyboardAvoidingView ref={ref} scrollRef={scrollRef}>
        <HeaderCreatePost
          onPress={onPressSubmit}
          title={'Create an event'}
          active={animatedActiveBtn}
          id={id}
          loading={loading}
          onGoBack={onGoBack}
        />
        <LoadingModal isVisible={loading} />
        <SScrollView ref={scrollRef} keyboardShouldPersistTaps={'handled'}>
          <SContainer>
            <SField>
              <STitle>General information</STitle>
              <AvatarWithSelectChannel
                bottomSheetRef={bottomSheetRef}
                label={label}
                typePost={'an event'}
              />
              <CheckboxWithText
                params={params}
                keyName={'privacy'}
                title={'Only attendees can view this event'}
              />
              <MyInputBackground
                onSelectedImage={onSelectBackground}
                image={params.value['cover-bg']}
              />
              <UIInput.Text
                title={'Title'}
                keyName={'name'}
                params={params}
                placeholder={'---'}
                defaultValue={''}
                onChangeValue={onChangeValue}
                required
              />
              {/*<SWrapperInputDate>*/}
              {/*  <UIInput.DateTime*/}
              {/*    title={'Date'}*/}
              {/*    keyName={'time-date'}*/}
              {/*    control={control}*/}
              {/*    placeholder={'---'}*/}
              {/*    defaultValue={''}*/}
              {/*    required*/}
              {/*    isDateTimeInput*/}
              {/*    modeDateTime={'date'}*/}
              {/*    style={{flex: 1}}*/}
              {/*  />*/}
              {/*  <CustomInput*/}
              {/*    title={'Time'}*/}
              {/*    keyName={'time-time'}*/}
              {/*    control={control}*/}
              {/*    placeholder={'---'}*/}
              {/*    defaultValue={''}*/}
              {/*    required*/}
              {/*    isDateTimeInput*/}
              {/*    modeDateTime={'time'}*/}
              {/*    style={{flex: 1, marginLeft: 12}}*/}
              {/*  />*/}
              {/*</SWrapperInputDate>*/}
              <UIInput.Text
                title={'Location'}
                keyName={'address'}
                params={params}
                placeholder={'---'}
                defaultValue={''}
                onChangeValue={onChangeValue}
              />
              <UITextInput
                title={'Content'}
                keyName={'content'}
                params={params}
                defaultValue={''}
                onChangeValue={onChangeValue}
              />
              <UIInput.Attachment
                title={'Attach files or Images'}
                keyName={'file[]'}
                params={params}
                onChangeValue={onChangeValue}
              />
            </SField>
            <SField>
              <STitle>Attendees</STitle>
              <UIInput.UserPicker
                title={'Managers'}
                keyName={'owners'}
                params={params}
                multiple
                onChangeValue={onChangeValue}
                type={'user'}
              />
              <UIInput.UserPicker
                title={'Invited guests'}
                type={'user'}
                keyName={'guests'}
                multiple
                params={params}
                onChangeValue={onChangeValue}
              />
              <CheckboxWithText
                params={params}
                keyName={'added_by_guests'}
                title={'Allow guests to add more guests'}
              />
            </SField>
            <AnimatedBottomSpace />
          </SContainer>
        </SScrollView>
        <DiscardDialog
          title={'Discard change'}
          content={
            'You have made changes to this item that have not been saved. If you leave this page, your changes will be lost.'
          }
          visible={visibleDialog}
          onClose={hideDialog}
        />
        <BottomSheetMenu
          name={'channel'}
          ref={bottomSheetRef}
          items={channelsOption}
          renderMenuItem={renderItem}
          titleHeader={'Select channel'}
        />
      </MyKeyboardAvoidingView>
    </FullScreenWrapper>
  );
});

const SContainer = styled(Animated.View)`
  flex: 1;
  padding: 12px 16px;
`;

const SField = styled.View`
  background-color: ${p => p.theme.white};
  gap: 12px;
`;

const STitle = styled.Text`
  font-family: ${Fonts.Medium};
  font-size: 16px;
  line-height: 24px;
  color: ${p => p.theme.neutral1};
`;

const SScrollView = styled.ScrollView`
  flex: 1;
  background-color: white;
  margin-top: 8px;
`;
export default CreateEventScreen;
